<template>
  <div id="app">
    <el-menu
      :default-active="activeIndex"
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
    >
      <el-menu-item index="1">日志输出</el-menu-item>
      <el-menu-item index="2">网络请求</el-menu-item>
      <!-- <el-menu-item index="4">
        <a href="https://github.com/y500/iOSDebugDatabase" target="_blank">数据库调试</a>
      </el-menu-item>-->
    </el-menu>
    <LogView v-if="activeIndex == '1'" :logList="logList" />
    <div v-if="activeIndex == '2'">
      <NetworkView :networkRequest="networkRequest" @showNetworkDetail="showNetworkDetail"></NetworkView>
      <NetworkDetailView v-if="showDetail" :requestDetail="detailData"></NetworkDetailView>
    </div>
  </div>
</template>

<script>
import LogView from "./components/log-view.vue";
import NetworkView from "./components/network-view.vue";
import NetworkDetailView from "./components/network-detail.vue";

import Vue from "vue";

import { Menu, MenuItem, Submenu } from "element-ui";
import "element-ui/lib/theme-chalk/index.css";

Vue.use(Menu);
Vue.use(MenuItem);
Vue.use(Submenu);

export default {
  name: "App",
  components: {
    LogView,
    NetworkView,
    NetworkDetailView,
  },
  data() {
    return {
      networkRequest: {},
      logList: [],
      showDetail: false,
      detailData: {},
      activeIndex: "1",
    };
  },
  mounted() {
    var host = document.location.hostname;
    var ws = new WebSocket("ws://"+host+":9000", "echo-protocol");
    ws.onopen = () => {
      this.receiveLog({ message: "CONNECT", color: "#00ff00" });
    };
    ws.onclose = () => {
      this.receiveLog({ message: "DISCONNECT", color: "#ff0000" });
    };
    ws.onmessage = (event) => {
      this.eventHandler(event);
    };
  },
  methods: {
    receiveLog: function (msg) {

      this.logList.push(msg);
      

      // var pre = document.createElement("pre");
      // pre.innerHTML = msg.message;
      // pre.style.cssText = "color:" + msg.color + ";";

      // var pre_css = document.createAttribute("class");
      // pre_css.nodeValue = "pre";
      // pre.setAttributeNode(pre_css);

      // document.getElementById("log").appendChild(pre);
    },
    eventHandler: function (event) {
      const result = JSON.parse(event.data);
      console.log("====", JSON.stringify(event.data), typeof result);
      if (result && typeof result === "string") {
        this.receiveLog({ message: event.data, color: "#333333" });
      } else {
        switch (result.type) {
          case "log":
            this.receiveLog(result);
            break;
          case "network":
            this.$set(this.networkRequest, result.taskID, result);
            break;
          default:
            break;
        }
      }

      console.log(JSON.stringify(this.networkRequest));
    },
    showNetworkDetail: function (rowData) {
      this.detailData = rowData.fullData;
      this.showDetail = true;
    },
    handleSelect: function (index) {
      console.log(index);
      this.activeIndex = index;
    },
  },
};
</script>

